* {
    font-family: 'SFPro-Display', sans-serif;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.admin-panel {
	height: 100vh;
	width: 100vw;
	display: grid;
	grid-template-columns: 200px 1fr;
}

.wrapper-panel {
	height: 100%;
	width: 200px;
	background: #906CD7;
}

.wrapper-iframe {
	position: relative;
	width: 100%;
}

.iframe-main {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0px;
	left: 0px;
}


